import React, { useContext, useState, useEffect } from "react";
import Context from "../utils/context";

import { Link } from "react-router-dom";
import history from "../utils/history";
import axios from "axios";

import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";

const Profile = () => {
  const context = useContext(Context);

  const [stateLocal, setState] = useState({
    open: false,
    resource_id: null,
    resources: [],
  });

  useEffect(() => {
    let mounted = true;
    const user_id = context.dbProfileState[0].uid;
    axios
      .get("/api/get/userresources", { params: { user_id: user_id } })
      .then((res) => {
        if (mounted) {
          setState({ ...stateLocal, resources: [...res.data] });
        }
      })
      .catch((err) => console.log(err));
    return () => (mounted = false);
  }, [context.dbProfileState]);

  const handleClickOpen = (rid) => {
    setState({ open: true, resource_id: rid });
  };

  const handleClickClose = () => {
    setState({ open: false, resource_id: null });
  };

  const DeleteResource = () => {
    const resource_id = stateLocal.resource_id;
    axios
      .delete("api/delete/resourcecomments", {
        data: { resource_id: resource_id },
      })
      .then(() =>
        axios
          .delete("/api/delete/resource", {
            data: { resource_id: resource_id },
          })
          .then((res) => console.log(res))
      )
      .catch((err) => console.log(err))
      .then(() => handleClickClose())
      .then(() => setTimeout(() => history.replace("/"), 700));
  };

  const RenderProfile = (props) => {
    return (
      <div className="justify-center">
        <h1>{props.profile.profile.nickname}</h1>
        <br />
        <img src={props.profile.profile.picture} alt="" />
        <br />
        <h4> {props.profile.profile.email}</h4>
        <br />
        <h5> {props.profile.profile.name} </h5>
        <br />
        <h6> Email Verified: </h6>
        {props.profile.profile.email_verified ? <p>Yes</p> : <p>No</p>}
        <br />
      </div>
    );
  };

  const RenderResources = (resource) => (
    <div>
      <Link
        to={{
          pathname: "/resource/" + resource.resource.rid,
          state: { resource },
        }}
      >
        {resource.resource.title}
      </Link>

      <div className="flex content-center">
        <div>{resource.resource.date_created}</div>
        <div>
          <Link
            to={{
              pathname: "/editresource/" + resource.resource.rid,
              state: { resource },
            }}
          >
            <button>Edit</button>
          </Link>
          <button onClick={() => handleClickOpen(resource.resource.rid)}>
            Delete
          </button>
        </div>
      </div>
      <div />
      <br />
      <div>
        <span style={{ overflow: "hidden" }}> {resource.resource.body} </span>
      </div>
    </div>
  );

  return (
    <div className="p-24">
      <div>
        <Link
          to={{ pathname: "/showmessages/" + context.dbProfileState[0].uid }}
        >
          <Button variant="contained" color="primary" type="submit">
            Show Messages
          </Button>
        </Link>
        <RenderProfile profile={context.profileState} />
      </div>
      <div>
        {stateLocal.resources
          ? stateLocal.resources.map((resource) => (
              <RenderResources resource={resource} key={resource.rid} />
            ))
          : null}
      </div>
      <Dialog
        open={stateLocal.open}
        onClose={handleClickClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title"> Confirm Delete? </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Deleteing Resource
          </DialogContentText>
          <DialogActions>
            <Button onClick={() => DeleteResource()}>Agree</Button>
            <Button onClick={() => handleClickClose()}>Cancel</Button>
          </DialogActions>
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default Profile;
