import React, { useContext, useState } from "react";
import { Link } from "react-router-dom";
import Context from "../utils/context";

const Header = () => {
  const context = useContext(Context);

  const [isExpanded, toggleExpansion] = useState(false);

  return (
    <nav className="flex items-center justify-between flex-wrap bg-red-500 p-6 px-12 xl:px-24">
      <div className="flex items-center flex-shrink-0 text-white mr-6">
        <svg
          className="fill-current h-8 w-8 mr-2"
          viewBox="0 0 65 65"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="M63.95,15.786c0,4.006-12.963,7.238-28.949,7.238 c-15.988,0-28.951-3.232-28.951-7.238l9.65,43.839c0,2.672,8.637,4.826,19.301,4.826c10.662,0,19.299-2.154,19.299-4.826l0,0      L63.95,15.786z" />
          <path d="M63.95,12.786c0-4.004-12.963-7.237-28.949-7.237 c-15.988,0-28.951,3.233-28.951,7.237c0,4.006,12.963,7.238,28.951,7.238C50.987,20.024,63.95,16.792,63.95,12.786L63.95,12.786      z" />
        </svg>
        <span className="font-semibold text-xl tracking-tight">Bukit</span>
      </div>
      <div className="block lg:hidden">
        <button
          onClick={() => toggleExpansion(!isExpanded)}
          className="flex px-3 py-2 border rounded text-red-100 border-red-400 hover:text-white hover:border-white"
        >
          <svg
            className="fill-current h-3 w-3"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
          >
            <title>Menu</title>
            <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
          </svg>
        </button>
      </div>
      <div
        className={`${
          isExpanded ? `block` : `hidden`
        } w-full block flex-grow lg:flex lg:items-center lg:w-auto`}
      >
        <div className="text-sm lg:flex-grow"></div>
        <Link
          to="/"
          className="block mt-4 lg:inline-block lg:mt-0 text-red-100 hover:text-white mr-4"
        >
          Home
        </Link>
        <Link
          to="/resources"
          className="block mt-4 lg:inline-block lg:mt-0 text-red-100 hover:text-white mr-4"
        >
          Resources
        </Link>
        <Link
          to="/profile"
          className="block mt-4 lg:inline-block lg:mt-0 text-red-100 hover:text-white mr-4"
        >
          Profile
        </Link>
      </div>
      <div>
        {!context.authState ? (
          <button
            className={`${
              isExpanded ? `block` : `hidden`
            } inline-block text-sm px-4 py-2 leading-none border rounded text-white border-white hover:border-transparent hover:text-red-500 hover:bg-white mt-4 lg:mt-0`}
            onClick={() => context.authObj.login()}
          >
            Login
          </button>
        ) : (
          <button
            className={`${
              isExpanded ? `block` : `hidden`
            } inline-block text-sm px-4 py-2 leading-none border rounded text-white border-white hover:border-transparent hover:text-red-500 hover:bg-white mt-4 lg:mt-0`}
            onClick={() => context.authObj.logout()}
          >
            Logout
          </button>
        )}
      </div>
    </nav>
  );
};

export default Header;
