import React, { useContext } from "react";
import Context from "../utils/context";

const SignUp = (props) => {
  const context = useContext(Context);
  return (
    <div className="flex">
      <div className="px-8 py-12 max-w-md mx-auto sm:max-w-xl lg:max-w-full lg:w-1/2 lg:py-24 lg:px-12">
        <div className="xl:max-w-lg xl:ml-auto">
          <img
            className="mt-6 rounded-lg shadow-xl sm:mt-8 sm:h-64 sm:w-full sm:object-cover sm:object-center lg:hidden"
            src="https://source.unsplash.com/user/itfeelslikefilm"
            alt="Random Unsplash"
          />
          <h1 className="mt-6 text-2xl font-bold text-gray-900 leading-tight sm:mt-8 sm:text-4xl lg:text-3xl">
            Find your way to learn.
            <br className="hidden lg:inline" />
            <span className="text-red-500"> Dive into knowledge.</span>
          </h1>
          <p className="mt-2 text-gray-600 sm:mt-4 sm:text-xl">
            Bukit helps you find resources to plan what you want to learn next.
            Dive into your favorite topic and find what helps you learn best.
          </p>
          <div className="mt-4 sm:mt-6">
            <div className=" py-12 max-w-md sm:max-w-xl lg:max-w-full lg:w-1/2">
              <button
                onClick={() => context.authObj.login()}
                className="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded"
              >
                Signup
              </button>
            </div>
          </div>
        </div>
      </div>
      <div className="hidden lg:block lg:w-1/2 lg:relative">
        <img
          className="absolute inset-0 h-full w-full object-cover object-center"
          src="https://source.unsplash.com/user/itfeelslikefilm"
          alt="A bunch of books layed out"
        />
      </div>
    </div>
  );
};

export default SignUp;
