import React, { useContext, useEffect, useState } from "react";

import { Link } from "react-router-dom";

import axios from "axios";
import Context from "../utils/context";
import ResourceCard from "./resourceCard";

import TextField from "@material-ui/core/TextField";

const Resources = (props) => {
  const context = useContext(Context);

  const [stateLocal, setState] = useState({
    resources: [],
    fetched: false,
    first_page_load: false,
    pages_slice: [1, 2, 3, 4, 5],
    max_page: null,
    items_per_page: 3,

    currentPage: 1,
    num_resources: null,
    resources_slice: null,
    resources_search: [],
    resources_per_page: 3,
  });

  useEffect(() => {
    if (!context.resourcesState) {
      axios
        .get("/api/get/allresources")
        .then((res) => context.handleAddResources(res.data))
        .catch((err) => console.log(err));
    }
    if (context.resourcesState && !stateLocal.fetched) {
      const indexOfLastResource = 1 * stateLocal.resources_per_page;
      const indexOfFirstResource =
        indexOfLastResource - stateLocal.resources_per_page;
      const last_page = Math.ceil(
        context.resourcesState.length / stateLocal.resources_per_page
      );

      setState({
        ...stateLocal,
        fetched: true,
        resources: [...context.resourcesState],
        num_resources: context.resourcesState.length,
        max_page: last_page,
        resources_slice: context.resourcesState.slice(
          indexOfFirstResource,
          indexOfLastResource
        ),
      });
    }
  }, [context, stateLocal]);

  useEffect(() => {
    let page = stateLocal.currentPage;
    let indexOfLastResource = page * 3;
    let indexOfFirstResource = indexOfLastResource - 3;

    setState({
      ...stateLocal,
      resources_slice: stateLocal.resources.slice(
        indexOfFirstResource,
        indexOfLastResource
      ),
    });
  }, [stateLocal.currentPage]); //eslint-disable-line

  const add_search_resources_to_state = (resources) => {
    setState({ ...stateLocal, resources_search: [] });
    setState({ ...stateLocal, resources_search: [...resources] });
  };

  const handleSearch = (event) => {
    setState({ ...stateLocal, resources_search: [] });
    const search_query = event.target.value;
    axios
      .get("/api/get/searchresource", {
        params: { search_query: search_query },
      })
      .then((res) =>
        res.data.length !== 0 ? add_search_resources_to_state(res.data) : null
      )
      .catch(function (error) {
        console.log(error);
      });
  };

  const page_change = (page) => {
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });

    //variables for page change
    let next_page = page + 1;
    let prev_page = page - 1;

    //handles general page change
    if (page > 2 && page < stateLocal.max_page - 1) {
      setState({
        ...stateLocal,
        currentPage: page,
        pages_slice: [prev_page - 1, prev_page, page, next_page, next_page + 1],
      });
    }
    if (page === 2) {
      setState({
        ...stateLocal,
        currentPage: page,
        pages_slice: [prev_page, page, next_page, next_page + 1, next_page + 2],
      });
    }
    //handles use case for user to go back to first page from another page
    if (page === 1) {
      setState({
        ...stateLocal,
        currentPage: page,
        pages_slice: [
          page,
          next_page,
          next_page + 1,
          next_page + 2,
          next_page + 3,
        ],
      });
    }
    //handles last page change
    if (page === stateLocal.max_page) {
      setState({
        ...stateLocal,
        currentPage: page,
        pages_slice: [
          prev_page - 3,
          prev_page - 2,
          prev_page - 1,
          prev_page,
          page,
        ],
      });
    }
    if (page === stateLocal.max_page - 1) {
      setState({
        ...stateLocal,
        currentPage: page,
        pages_slice: [prev_page - 2, prev_page - 1, prev_page, page, next_page],
      });
    }
  };

  return (
    <div className="p-24">
      <div
        style={{ opacity: stateLocal.opacity, transition: "opacity 2s ease" }}
      >
        {context.authState ? (
          <Link to="/addresource">
            <button className="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded">
              Add Resource
            </button>
          </Link>
        ) : (
          <Link to="/signup">
            <button className="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded">
              Sign Up To Add Resource
            </button>
          </Link>
        )}
      </div>
      <br />

      <TextField
        id="search"
        label="Search"
        margin="normal"
        onChange={handleSearch}
      />
      <br />
      <div className="flex space-x-4">
        {stateLocal.resources_search
          ? stateLocal.resources_search.map((resource) => (
              <ResourceCard key={resource.rid + 1000} resource={resource} />
            ))
          : null}
      </div>

      <h1 className="text-3xl font-bold">Resources</h1>
      <div className="flex space-x-4">
        {stateLocal.resources_slice
          ? stateLocal.resources_slice.map((resource) => (
              <ResourceCard key={resource.rid} resource={resource} />
            ))
          : null}
      </div>
      <div>
        <div className="FlexRow">
          <button onClick={() => page_change(1)}> First </button>
          <button onClick={() => page_change(stateLocal.currentPage - 1)}>
            {" "}
            Prev{" "}
          </button>
          {stateLocal.pages_slice.map((page) => (
            <div
              onClick={() => page_change(page)}
              className={
                stateLocal.currentPage === page
                  ? "pagination-active"
                  : "pagination-item"
              }
              key={page}
            >
              {page}
            </div>
          ))}
          <button onClick={() => page_change(stateLocal.currentPage + 1)}>
            {" "}
            Next{" "}
          </button>
          <button onClick={() => page_change(stateLocal.max_page)}>
            {" "}
            Last{" "}
          </button>
        </div>
      </div>
    </div>
  );
};

export default Resources;
