import React, { useContext, useState, useEffect } from "react";

import { Link } from "react-router-dom";
import axios from "axios";
import history from "../utils/history";
import Context from "../utils/context";

import TextField from "@material-ui/core/TextField";

import Button from "@material-ui/core/Button";

const ShowResource = (props) => {
  const context = useContext(Context);

  const [stateLocal, setState] = useState({
    comment: "",
    fetched: false,
    cid: 0,
    delete_comment_id: 0,
    edit_comment_id: 0,
    edit_comment: "",
    comments_arr: null,
    cur_user_id: null,
    like_resource: true,
    likes: 0,
    like_user_ids: [],
    resource_title: null,
    resource_body: null,
    resource_author: null,
    resource_id: null,
  });

  useEffect(() => {
    if (props.location.state && !stateLocal.fetched) {
      setState({
        ...stateLocal,
        fetched: true,
        likes: props.location.state.resource.resource.likes,
        like_user_ids: props.location.state.resource.resource.like_user_id,
        resource_title: props.location.state.resource.resource.title,
        resource_body: props.location.state.resource.resource.body,
        resource_author: props.location.state.resource.resource.author,
        resource_id: props.location.state.resource.resource.rid,
      });
    }
  }, [stateLocal, props.location]);

  useEffect(() => {
    if (!props.location.state && !stateLocal.fetched) {
      console.log("yyyy");
      const resource_id = props.location.pathname.substring(6);

      axios
        .get("/api/get/resource", { params: { resource_id: resource_id } })
        .then((res) =>
          res.data.length !== 0
            ? setState({
                ...stateLocal,
                fetched: true,
                likes: res.data[0].likes,
                like_user_ids: res.data[0].like_user_id,
                resource_title: res.data[0].title,
                resource_body: res.data[0].body,
                resource_author: res.data[0].author,
                resource_id: res.data[0].rid,
              })
            : null
        )
        .catch((err) => console.log(err));
    }
  }, [stateLocal, props.location]);

  useEffect(() => {
    if (!stateLocal.comments_arr) {
      if (props.location.state) {
        const resource_id = props.location.pathname.substring(10);
        axios
          .get("/api/get/allresourcecomments", {
            params: { resource_id: resource_id },
          })
          .then((res) =>
            res.data.length !== 0
              ? setState({ ...stateLocal, comments_arr: [...res.data] })
              : null
          )
          .catch((err) => console.log(err));
      }
    }
  }, [props.location, stateLocal]);

  const handleCommentSubmit = (submitted_comment) => {
    if (stateLocal.comments_arr) {
      setState({
        ...stateLocal,
        comments_arr: [submitted_comment, ...stateLocal.comments_arr],
      });
    } else {
      setState({ ...stateLocal, comments_arr: [submitted_comment] });
    }
  };

  const handleCommentUpdate = (comment) => {
    const commentIndex = stateLocal.comments_arr.findIndex(
      (com) => com.cid === comment.cid
    );
    var newArr = [...stateLocal.comments_arr];
    newArr[commentIndex] = comment;

    setTimeout(
      () =>
        setState({
          ...stateLocal,
          comments_arr: [...newArr],
          edit_comment_id: 0,
        }),
      100
    );
  };

  const handleCommentDelete = (cid) => {
    setState({ ...stateLocal, delete_comment_id: cid });
    const newArr = stateLocal.comments_arr.filter((com) => com.cid !== cid);
    setState({ ...stateLocal, comments_arr: newArr });
  };

  const handleEditFormClose = () => {
    setState({ ...stateLocal, edit_comment_id: 0 });
  };

  const RenderComments = (props) => {
    return (
      <div>
        <div>
          <p>{props.comment.comment} </p>
          <small>
            {props.comment.date_created === "Just Now" ? (
              <div>
                {" "}
                {props.comment.isEdited ? (
                  <span> Edited </span>
                ) : (
                  <span> Just Now </span>
                )}
              </div>
            ) : (
              props.comment.date_created
            )}
          </small>
          <p> By: {props.comment.author} </p>
        </div>
        <div>
          {props.cur_user_id === props.comment.user_id ? (
            !props.isEditing ? (
              <div>
                <Button
                  onClick={() =>
                    setState({
                      ...stateLocal,
                      edit_comment_id: props.comment.cid,
                      edit_comment: props.comment.comment,
                    })
                  }
                >
                  Edit
                </Button>
              </div>
            ) : (
              <form
                onSubmit={(event, cid) =>
                  handleUpdate(event, props.comment.cid)
                }
              >
                <input
                  autoFocus={true}
                  name="edit_comment"
                  id="edited_comment"
                  label="Comment"
                  value={stateLocal.edit_comment}
                  onChange={handleEditCommentChange}
                />
                <br />
                <Button type="submit">Agree</Button>
                <Button type="button" onClick={handleEditFormClose}>
                  Cancel
                </Button>
                <button onClick={() => handleDeleteComment(props.comment.cid)}>
                  Delete
                </button>
              </form>
            )
          ) : null}
        </div>
      </div>
    );
  };

  const handleEditCommentChange = (event) =>
    setState({ ...stateLocal, edit_comment: event.target.value });

  const handleSubmit = (event) => {
    event.preventDefault();
    setState({ ...stateLocal, comment: "" });

    const comment = event.target.comment.value;
    const user_id = context.dbProfileState[0].uid;
    const username = context.dbProfileState[0].username;
    const resource_id = stateLocal.resource_id;
    const current_time = "Just Now";
    const temp_cid = Math.floor(Math.random() * 1000);

    const submitted_comment = {
      cid: temp_cid,
      comment: comment,
      user_id: user_id,
      author: username,
      date_created: current_time,
    };

    const data = {
      comment: event.target.comment.value,
      resource_id: resource_id,
      user_id: user_id,
      username: username,
    };

    axios
      .post("/api/post/commenttodb", data)
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
    window.scroll({ top: 0, left: 0, behavior: "smooth" });
    handleCommentSubmit(submitted_comment);
  };

  const handleUpdate = (event, cid) => {
    event.preventDefault();
    console.log(event);
    console.log(cid);
    const comment = event.target.edited_comment.value;
    const comment_id = cid;
    const resource_id = stateLocal.resource_id;
    const user_id = context.dbProfileState[0].uid;
    const username = context.dbProfileState[0].username;
    const isEdited = true;
    const current_time = "Just Now";

    const edited_comment = {
      cid: comment_id,
      comment: comment,
      user_id: user_id,
      author: username,
      date_created: current_time,
      isEdited: isEdited,
    };

    const data = {
      cid: comment_id,
      comment: comment,
      resource_id: resource_id,
      user_id: user_id,
      username: username,
    };

    axios
      .put("/api/put/commenttodb", data)
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
    handleCommentUpdate(edited_comment);
  };

  const handleDeleteComment = (cid) => {
    const comment_id = cid;
    console.log(cid);
    axios
      .delete("/api/delete/comment", { data: { comment_id: comment_id } })
      .then((res) => console.log(res))
      .catch((err) => console.log(err));
    handleCommentDelete(cid);
  };

  const handleLikes = () => {
    const user_id = context.dbProfileState[0].uid;
    const resource_id = stateLocal.resource_id;

    const data = { uid: user_id, resource_id: resource_id };
    console.log(data);
    if (!stateLocal.like_user_ids.includes(user_id)) {
      axios
        .put("/api/put/likes", data)
        .then(
          !stateLocal.like_user_ids.includes(user_id) &&
            stateLocal.like_resource
            ? setState({
                ...stateLocal,
                likes: stateLocal.likes + 1,
                like_resource: false,
              })
            : null
        )
        .catch((err) => console.log(err));
    }
  };

  return (
    <div>
      <div>
        <h2>Resource</h2>
        {stateLocal.comments_arr || props.location.state ? (
          <div>
            <p>{stateLocal.resource_title}</p>
            <p>{stateLocal.resource_body}</p>
            <p>{stateLocal.resource_author}</p>
          </div>
        ) : null}

        <div
          style={{ cursor: "pointer" }}
          onClick={
            context.authState
              ? () => handleLikes()
              : () => history.replace("/signup")
          }
        >
          <svg
            className="h-12 w-12 text-teal-500 fill-current"
            viewBox="0 0 24 24"
          >
            <path d="M8.128 19.825a1.586 1.586 0 0 1-1.643-.117 1.543 1.543 0 0 1-.53-.662 1.515 1.515 0 0 1-.096-.837l.736-4.247-3.13-3a1.514 1.514 0 0 1-.39-1.569c.09-.271.254-.513.475-.698.22-.185.49-.306.776-.35L8.66 7.73l1.925-3.862c.128-.26.328-.48.577-.633a1.584 1.584 0 0 1 1.662 0c.25.153.45.373.577.633l1.925 3.847 4.334.615c.29.042.562.162.785.348.224.186.39.43.48.704a1.514 1.514 0 0 1-.404 1.58l-3.13 3 .736 4.247c.047.282.014.572-.096.837-.111.265-.294.494-.53.662a1.582 1.582 0 0 1-1.643.117l-3.865-2-3.865 2z" />
          </svg>
          <small className="notification-num-showresource">
            {stateLocal.likes}
          </small>
        </div>
      </div>
      <div>
        <h2> Comments:</h2>
        {stateLocal.comments_arr
          ? stateLocal.comments_arr.map((comment) => (
              <RenderComments
                comment={comment}
                cur_user_id={
                  context.dbProfileState ? context.dbProfileState[0].uid : null
                }
                key={comment.cid}
                isEditing={
                  comment.cid === stateLocal.edit_comment_id ? true : false
                }
              />
            ))
          : null}
      </div>
      <div>
        <form onSubmit={handleSubmit}>
          <TextField id="comment" label="Comment" margin="normal" />
          <br />
          <div>{console.log("Loaded Res")}</div>
          {context.authState ? (
            <Button variant="contained" color="primary" type="submit">
              Submit
            </Button>
          ) : (
            <Link to="/signup">
              <Button variant="contained" color="primary">
                Signup to Comment
              </Button>
            </Link>
          )}
        </form>
      </div>
      <div></div>
    </div>
  );
};

export default ShowResource;
