import React, { useContext } from "react";
import { Router, Route, Switch, Redirect } from "react-router";
import history from "./utils/history";

import Context from "./utils/context";
import AuthCheck from "./utils/authcheck";

import Home from "./hooks/home";
import Header from "./hooks/header";
import Callback from "./hooks/callback";
import SignUp from "./hooks/signup";

import Profile from "./Profile/profile";
import ShowUser from "./Profile/showuser";
import SendMessage from "./Profile/sendmessage";
import ShowMessages from "./Profile/showmessages";
import ReplytoMessage from "./Profile/replytomessage";

import Resources from "./Resources/resources";
import AddResource from "./Resources/addresource";
import ShowResource from "./Resources/showresource";
import EditResource from "./Resources/editresource";

const PrivateRoute = ({ component: Component, auth }) => (
  <Route
    render={(props) =>
      auth === true ? (
        <Component auth={auth} {...props} />
      ) : (
        <Redirect to={{ pathname: "/signup" }} />
      )
    }
  />
);

const Routes = () => {
  const context = useContext(Context);

  return (
    <div className="bg-blue-200 h-screen">
      <Router history={history}>
        <Header />
        <div className="justify-center bg-gray-100">
          <Switch>
            <Route exact path="/" component={Home} />

            <Route path="/authcheck" component={AuthCheck} />
            <Route path="/signup" component={SignUp} />

            <Route path="/resources" component={Resources} />
            <Route path="/resource/:rid" component={ShowResource} />
            <Route path="/editresource/:rid" component={EditResource} />
            <Route path="/addresource" component={AddResource} />

            <Route path="/user/:name" component={ShowUser} />

            <PrivateRoute
              path="/profile"
              auth={context.authState}
              component={Profile}
            />

            <PrivateRoute
              path="/sendmessage"
              auth={context.authState}
              component={SendMessage}
            />

            <PrivateRoute
              path="/showmessages/:id"
              auth={context.authState}
              component={ShowMessages}
            />
            <PrivateRoute
              path="/replytomessage"
              auth={context.authState}
              component={ReplytoMessage}
            />

            <PrivateRoute
              path="/profile"
              auth={context.authState}
              component={Profile}
            />
            <Route
              path="/callback"
              render={(props) => {
                context.handleAuth(props);
                return <Callback />;
              }}
            />
          </Switch>
        </div>
      </Router>
    </div>
  );
};

export default Routes;
