import * as ACTION_TYPES from "../actions/action_types";

export const initialState = {
  resources: null,
};

export const ResourcesReducer = (state = initialState, action) => {
  switch (action.type) {
    case ACTION_TYPES.FETCH_DB_RESOURCES:
      return {
        ...state,
        resources: action.payload,
      };
    case ACTION_TYPES.REMOVE_DB_RESOURCES:
      return {
        ...state,
        resources: [],
      };

    default:
      return state;
  }
};
